<script>
export default {
  created () {
    // 调用API从本地缓存中获取数据
    const logs = wx.getStorageSync('logs') || []
    logs.unshift(Date.now())
    wx.setStorageSync('logs', logs)
    const userInfo = wx.getStorageSync('userInfo') || ''
    if (userInfo === '') {
      wx.login({
        success: () => {
          wx.getUserInfo({
            success: (res) => {
              wx.setStorageSync('userInfo', res.userInfo)
            }
          })
        }
      })
    };
    console.log(wx.getStorageSync('userInfo'))
  }
}
</script>

